@import "../../styles/AppDesign.scss";
$sidebarWidth:70px;
$sidebarWidthTb:50px;
$sidebarWidthMb:45px;
.sidebar{
  position:fixed;
  left:0;
  top:0;
  height:100%;
  z-index: 1000;
  padding:1rem;
  pointer-events: none;
  width:$sidebarWidth;
  @include tabletView(){
    width:$sidebarWidthTb;
  }
  @include mobileView(){
    width:$sidebarWidthMb;
    padding: 0 12.5px;
  }
  .page-ready &{
    pointer-events: all;
  }
  &:after{
    content:'';
    position: absolute;
    display:block;
    top:0;
    right:0;
    width:1px;
    height:100%;
    background: $lineGray;
    transform-origin: center top;
    transition:1.5s ease-in;
    transform: scale(0);
    z-index: 20;
    .page-ready &{
      transform: scale(1);
    }
  }
}
.sidebar__overlay{
  position: fixed;
  z-index: 10;
  height: 100vh;
  width: 100%;
  top:0;
  left:0;
  transition: .4s;
  background: transparent;
  pointer-events: none;
  .sidebar--menu-open &{
    background: $main;
    pointer-events: all;
  }
  @include desktopView(){
    display:none !important;
  }
}

.sidebar__inner{
  position: relative;
  z-index: 20;
  width:100%;
  height:100%;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  align-items: center;
  padding-top:150px;
  padding-bottom:15px;
  @include tabletView(){
    padding-top:120px;
  }
  @include mobileView(){
    padding-top:90px;
  }
}

.sidebar__top{
  position: absolute;
  left:0;
  top:0;
  display:flex;
  flex-wrap: nowrap;
  align-items: center;
  @include mobileView(){
    padding-top:10px;
  }
}

.sidebar__menu-btn{
  position: relative;
  border:0;
  background: transparent;
  padding:0;
  cursor: pointer;
  margin-right:12.5px;
  opacity:0;
  .page-ready &{
    opacity:1;
  }
  @include desktopView(){
    display:none !important;
  }
}
.sidebar__menu-text{
  @include a11y-hidden();
}
.sidebar__menu-icon{
  $iconColor:#FFF;
  $lineHeight:1px;
  
  position: relative;
  display:block;
  width:100%;
  height:100%;
  top:0;
  left:0;
  transition: .4s;

  $size:25px;
  width:$size;
  height:$size;
  @include smallScreen(){
    $size:20px;
    width:$size;
    height:$size;
  }

  &:before{
    content:'';
    display:block;
    position:absolute;
    width:100%;
    left:0;
    top:50%;
    height:$lineHeight;
    margin-top:-$lineHeight/2;
    transition: .4s;
    background:$iconColor;
    .sidebar.sidebar--inverse-color:not(.sidebar--menu-open) &{
      background:$main;
    }
  }
  .sidebar__menu-lines{
    position: relative;
    display:block;
    width:100%;
    height:100%;
    top:0;
    left:0;
    &:before,
    &:after{
        content:'';
        display:block;
        position:absolute;
        width:100%;
        left:0;
        height:$lineHeight;
        margin-top:-$lineHeight/2;
        background:$iconColor;
        transition: .2s;
        transform-origin: left center;
        .sidebar.sidebar--inverse-color:not(.sidebar--menu-open) &{
          background:$main;
        }
    }
    &:before{
        top:20%;
        transform: scaleX(0.7);
    }
    &:after{
        bottom:20%;
        transform: scaleX(0.5);
    }
  }
  .sidebar--menu-open &{
      &:before{
        display:none;
      }
      .sidebar__menu-lines{
        transform: rotate(45deg);
        &:before,
        &:after{
          top:50%;
          left:0;
          bottom:auto;
          height:$lineHeight;
          margin-top:-$lineHeight/2;
          transform-origin: center center;
          transform: scaleX(1);
        }
        &:after{
          transform: rotate(90deg) scaleX(1);
        }
      }
    }
}

.sidebar__title{
  font-weight: bold;
  overflow: hidden;
  line-height: 1.35;
}

.sidebar__title-fragment{
  white-space: nowrap;
  transform:translateX(-100%);
  transition: .3s;
  .page-ready &{
    transform:translateX(0);
  }

}

.sidebar__navs{
  display: flex;
  flex-flow: column;
  align-items: center;
}

.sidebar__nav{
  position:relative;
  @include resetBtn();
  padding: 10px 0;
  $size:22px;
  $sizeS:16px;
  display:flex;
  align-items: center;
  &:before,
  &:after{
    content:'';
    display:block;
    width:$size;
    height:$size;
    border-radius: 100%;
    @include mobileView(){
      width:$sizeS;
      height:$sizeS;
    }
  }
  &:before{
    border:1px solid $lineGray;
    opacity: 0;
    transition:1s;
    transform: scale(8);

    .page-ready &{
      transform: scale(1);
      opacity: 1;
    }
  }
  &:after{
    background:#FFF;
    transform-origin: center center;
    transform:scale(0.5);
    position: absolute;
    top:50%;
    left:50%;
    margin-left:-$size/2;
    margin-top:-$size/2;
    @include mobileView(){
      margin-left:-$sizeS/2;
      margin-top:-$sizeS/2;
    }
    opacity:0;
    .sidebar.sidebar--inverse-color:not(.sidebar--menu-open) &{
      background:$main;
    }
  }
  $interval:.2s;
  @for $i from 1 through 10 {
    &:nth-child(#{$i}){
      &:before{
        transition-delay:$interval * ($i - 1);
      }
    }
  }
  &:last-child{
    margin-bottom:0;
  }

  &.is-active,
  &:hover{
    &:after{
      opacity:1;
    }
    .sidebar__nav-label{
      @include desktopView(){
        pointer-events: all;
        opacity:1;
      }
    }
  }

  &.is-active{
    pointer-events: none;
    > *{
      pointer-events: none !important;
    }
    .sidebar__nav-label{
      color:#FFF;
    }
  }
}
.sidebar__nav-label{
  position: absolute;
  left:100%;
  top:50%;
  transform: translateY(-50%);
  white-space: nowrap;
  margin-left:10px;
  pointer-events: none;
  opacity:0;
  transition: .4s;
  color:#000;
  font-size: $fontSizeM;
  text-transform: uppercase;
  @media (max-width: 1280px) {
    font-size: $fontSizeS;
  }
  @include mobileView(){
    padding-left:5px;
  }
  .sidebar.sidebar--inverse-color:not(.sidebar--menu-open) &{
    color:$main;
  }
  .sidebar--menu-open &,
  .sidebar.sidebar--menu-open.sidebar--inverse-color &{
    pointer-events: all;
    opacity:1;
  }
}
.sidebar__soc{
  display:block;
  @include resetBtn();
  $size:22px;
  width:$size;
  height:$size;
  font-size: 0;
  background: url() no-repeat center center;
  background-size:contain;
  margin-top:20px;
  transition: color .3s;
  opacity:0;
  .page-ready &{
    animation: basic-fade-in .4s .7s forwards;
    // .sidebar.sidebar--inverse-color:not(.sidebar--menu-open) &{
    //   color:$main;
    // }
  }
  &:hover{
    filter: invert(1);
  }

  &.sidebar__soc--fb{
    background-image: url(/img/icon/icon-soc-fb.svg);
  }
  &.sidebar__soc--ig{
    background-image: url(/img/icon/icon-soc-ig.svg);
  }
}

.sidebar__join{
  @include resetBtn();
  white-space: nowrap;
  transform-origin: center top;
  transform: rotate(180deg);
  position: absolute;
  margin-left:.2em;
  &:hover{
    color:#FFF;
    .sidebar.sidebar--inverse-color:not(.sidebar--menu-open) &{
      color:$main;
    }
  }
  transition: color .3s;
  opacity:0;
  .page-ready &{
    animation: basic-fade-in .4s .7s forwards;
  }
  @include tabletView(){
    margin-left:.4em;
  }
}
.sidebar__join-label{
  writing-mode: vertical-lr;
  font-weight: bold;
  // transform: scale(-1, 1);
}