
// Colors
$main: #D8182A;
$dark: #1d1d1b;

$colorTxt:#000;

$lightGray:#f8f8f8;
$mediumGray: #b6b4b4;
$darkGray: #747474;

$lineGray:rgba(#000, 0.25);

// Global Values
$gridBig: 1500px;
$gridMedium: 1280px;
$gridDesktop: 560px;

$headerHeightL: 90px;
$headerHeightM: 70px;
$headerHeightS: 55px;

// Fonts Size:
$fontSizeTitleXXL:10rem;
$fontSizeTitleXL:7rem;
$fontSizeTitleL:4rem;
$fontSizeTitleM:3rem;
$fontSizeTitleS:2.5rem;
$fontSizeTitleSS:1.3rem;
$fontSizeTitleXS:1rem;

$fontSizeXXL:2.5rem;
$fontSizeXL:2rem;
$fontSizeL:1.5rem;
$fontSizeM:1.3rem;
$fontSizeS:1rem;
$fontSizeSS:.9rem;
$fontSizeXS:.5rem;

// Fonts
@include fontFace("Myriad Pro", "/fonts/MyriadPro-Bold", normal, 200);
@include fontFace("Myriad Pro", "/fonts/MyriadPro-Regular", normal, 400);
@include fontFace("Myriad Pro", "/fonts/MyriadPro-Semibold", normal, 600);


$fontPrimary: "Myriad Pro", -apple-system,
BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
"Fira Sans", "Droid Sans", "Helvetica Neue",
"Microsoft JhengHei", 微軟正黑體,
sans-serif;

$fontSecondary: $fontPrimary; // TODO: remove if not in use