@import "../../styles/AppDesign.scss";

.letter-fader {
    position: relative;
    width:100%;
}
.letter-fader__grp{
    position: absolute;
    top:0;
    width:100%;
    opacity: 0;
    pointer-events: none;
    &.is-active{
        opacity: 1;
        pointer-events: all;
    }
}
.letter-fader__word{
    display:inline-block;
    white-space: nowrap;
}
.letter-fader__letter{
    display:inline-block;
    opacity:0;
    // transform: rotateY(100%);
    // transition: .4s;
    .letter-fader__grp.is-active &{
        opacity: 0;
        pointer-events: all;
        animation: letter-fader-fadein .4s forwards;
        @keyframes letter-fader-fadein {
            0%   {
                opacity:0;
                transform: translateY(16px);
            }
            100% {
                opacity:1;
                transform: translateY(0);
            }
        }    
    }
}
