
// Fonts Size:
$fontSizeTitleXXL:10rem;
$fontSizeTitleXL:6.5rem;
$fontSizeTitleL:4rem;
$fontSizeTitleM:3rem;
$fontSizeTitleS:2.5rem;
$fontSizeTitleSS:1.3rem;
$fontSizeTitleXS:1rem;

$fontSizeXXL:2.5rem;
$fontSizeXL:2rem;
$fontSizeL:1.5rem;
$fontSizeM:1.3rem;
$fontSizeS:1rem;
$fontSizeSS:.9rem;
$fontSizeXS:.5rem;

// Fonts
@include fontFace("Myriad Pro", "/fonts/MyriadPro-Bold", normal, 200);
@include fontFace("Myriad Pro", "/fonts/MyriadPro-Regular", normal, 400);
@include fontFace("Myriad Pro", "/fonts/MyriadPro-Semibold", normal, 600);


$fontPrimary: "Myriad Pro", -apple-system,
BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
"Fira Sans", "Droid Sans", "Helvetica Neue",
"Microsoft JhengHei", 微軟正黑體,
sans-serif;

$fontSecondary: $fontPrimary; // TODO: remove if not in use


@mixin secTitle() {
    font-size:$fontSizeTitleXL;
    text-transform: uppercase;
    line-height: 1;
    @include largeScreen(){
        font-size:$fontSizeTitleL + 1.5;
    }
    @include tabletView(){
        font-size:$fontSizeTitleL;
    }
    @include mobileView(){
        font-size:$fontSizeTitleM;
    }
    &-icon{
        display: block;
        $size:60px;
        width: $size;
        height: $size;
        margin-bottom:1.5rem;
        background: url('/img/gallery/icon-gallery.svg') no-repeat center center;
        background-size: contain;
        @include mobileView(){
            $size:40px;
            width: $size;
            height: $size;
        }
    }
    &-txt{
        letter-spacing: 0.02em;
    }
    &-tag{
        font-size:$fontSizeL;
        line-height: 1.5;
        margin-bottom:.5rem;
    }
    &-gray{
        opacity: .1;
    }
    &-tag,
    &-txt{
        display:block;
    }
}
@mixin secDesc() {
    font-size:$fontSizeM;
    line-height: 1.5;
    @include mobileView(){
        font-size:$fontSizeS;
    }
}