@import "../../styles/AppDesign.scss";

.sec-journey {
    position: relative;
    min-height: 100vh;
    width: 100%;
    z-index: 500;
    top: 0;
    left: 0;
    padding-top: 100px;
    padding-left: 30px;
}

.sec-journey__bg {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    pointer-events: none;
    z-index: 0;
    overflow: hidden;

    &:after {
        content: '';

        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background: url('/img/journey/bg-curve.svg') no-repeat right top;
        background-size: 100% auto;
        transition: .6s;
        z-index: 20;
    
        // opacity: 0;
        // .sec-journey.is-active & {
        //     opacity: 1;
        // }
    }
}

.sec-journey__bg-graphic {
    background: #FFF;
    height: 300vh;
    width: 300vh;
    position: absolute;
    left: 0;
    top: 0;
    transform-origin: left top;
    transform: rotate(90deg);
    transition: .6s;
    z-index: 10;

    .sec-journey.is-active & {
        transform: rotate(0);
    }
}

.sec-journey__inner {
    position: relative;
    margin: 0 auto;
    @include sec-grid();
}

.sec-journey__title {
    @include secTitle();
}

.sec-journey__desc {
    @include secDesc();
    position: relative;
    width: 50%;
    left: 50%;

    @include tabletView() {
        width: 100%;
        left: 0;
    }

    @include mobileView() {
        width: 100%;
        left: 0;
    }
}

.sec-journey__effect-typing {
    position: relative;
    display: block;
    height: 2em;

    .sec-journey__title-txt {
        top: 0;
        position: absolute;
        width: 100%;
    }
}

.sec-journey__effect-perspective {
    $total-lines: 2;
    $left-offset: -40px;
    $clip-height: 0.95em;
    $line-height: $clip-height;
    display: block;
    transform-origin: left top;
    transform: scale(0.75) translate3d(15%, 0, 0);
    -webkit-font-smoothing: antialiased;
    -webkit-font-kerning: normal;
    -webkit-text-size-adjust: 100%;
    width: 150%;
    margin-left: -25%;

    @include largeScreen() {
        width: 160%;
        margin-left: -30%;
        transform: scale(0.75) translate3d(15vw, 50%, 0);
    }
    @include tabletView() {
        width: 120%;
        margin-left: 0;
        transform: scale(0.75) translate3d(15vw, 50%, 0);
    }

    @media (max-width: 640px) {
        font-size: 1.2em;
        width: 200%;
        margin-left: -50%;
        padding-top: 45px;
        transform: scale(0.75) translate3d(80vw, 30%, 0);
    }

    @media (max-width: 480px) {
        transform: scale(0.75) translate3d(80vw, 20%, 0);
        font-size: 1em;
    }

    @media (max-width: 380px) {
        transform: scale(0.75) translate3d(85vw, 10%, 0);
        font-size: 0.8em;
    }

    @include desktopView() {
        .sec-journey__title:hover & {
            .sec-journey__title-txt {
                transform: translate3d(0, -($clip-height), 0);
            }
        }
    }

    @include mobileView() {
        .viewport-animtion:not(.is-active) & {
            .sec-journey__title-txt {
                transform: translate3d(0, -($clip-height), 0);
            }
        }
    }

    .sec-journey__line {
        height: $clip-height;
        display: block;
        overflow: hidden;
        position: relative;

        &:nth-child(odd) {
            transform: skew(-55deg, 20deg) scaleY(.6);
        }

        &:nth-child(even) {
            transform: skew(-0deg, 20deg) scaleY(1);
        }

        @for $i from 1 through $total-lines+1 {
            &:nth-child(#{$i}) {
                left: $left-offset * $i;
            }
        }
    }

    .sec-journey__title-txt {
        height: $clip-height;
        line-height: $line-height;
        transition: all .3s ease-in-out;
        transform: translate3d(0, 0, 0);
        vertical-align: top;
        white-space: nowrap;
    }
}