@import "../../styles/AppDesign.scss";
@import "../../styles/AppDesign.scss";

.sec-artisan {
    position: relative;
    min-height: 100vh;
    width: 100%;
    z-index: 550;
    top: 0;
    left: 0;
    padding-top: 100px;
    padding-bottom: 100px;
    padding-left: 30px;
    // background: #000;
    color: #FFF;

    @include tabletView() {
        padding-top: 60px;
        padding-bottom: 60px;
    }

    &__desktop-item {
        @include tabletView() {
            display: none;
        }
    }

    &__mobile-item {
        @include desktopView() {
            display: none;
        }
    }
}

.sec-artisan__bg{
    position: fixed;
    top: 0;
    left:0;
    height: 100%;
    width: 100%;
    pointer-events: none;
    z-index: 0;
    background: #000;
    overflow:hidden;
    transition: 1s;
    opacity:0;
    .sec-artisan.is-active &{
        opacity:1   
    }
}

.sec-artisan__inner {
    position: relative;
    margin: 0 auto;
    @include sec-grid();
}

.sec-artisan__title{
    @include secTitle();
}
.sec-artisan__desc{
    @include secDesc();
    position: relative;
}

.sec-artisan__content {
    display: flex;
    align-items: flex-start;

    @include tabletView() {
        width: 100%;
    }
}

.sec-artisan__graphics {
    position: relative;
    width: 50%;
    display: flex;
    justify-content: center;
    margin-top:3rem;

    @include tabletView() {
        display: none;
    }

    &--mobile {
        display: none;

        @include tabletView() {
            display: flex;
            margin: 120px auto 30px;
            width: 60%;
        }
    }
}

.sec-artisan__graphic {
    position: relative;
    margin-top: 40px;
}

.sec-artisan__graphic-mask {
    position: relative;
    max-width: 100%;
    z-index: 10;
}

.sec-artisan__graphic-blur {
    position: absolute;
    z-index: 5;
    left:0;
    top: 0;
    left: -40%;
    top: -15%;
    @include tabletView() {
        max-width: 125%;
        left: -60%;
        top: -30%;
    }
}

.sec-artisan__wave-tag {
    position: absolute;
    display: block;
    right: 100%;
    bottom: 100%;
    margin-right: -30px;
    margin-bottom: -30px;
    $size: 150px;
    width: $size;
    height: $size;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;
    transition: .4s;
    @include tabletView() {
        $size: 90px;
        width: $size;
        height: $size;
        left: 100%;
        right: auto;
        margin-right: 0;
        margin-left: -30px;
    }

    &:after,
    &:before {
        content: '';
        position: absolute;
        display: block;
        right: 0%;
        bottom: 0;
        width: 100%;
        height: 100%;
        border: 1px solid #FFF;
        border-radius: 100%;
    }
    &:after{
        opacity:.5;
        @keyframes wave-move {
            0%   { transform: scale(1); }
            100%   { transform: scale(1.6); opacity: 0; }
        }        
        animation: wave-move 1.5s ease-out infinite;
    }

    // .sec-artisan__graphic:hover & {
    //     transform: scale(1.2, 1.2);
    //     // z-index: 15;
    // }
}

.sec-artisan__wave-text {
    text-align: center;
    line-height: 1.5;
    font-size: $fontSizeM;
    transform: rotate(-15deg);
}

.sec-artisan__subs {
    $size: 120px;
    width: $size;
    height: $size;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;
    color: #FFF;
    background: $main;
    border-radius: 100%;
    text-decoration: none;
    text-align: center;

    .sec-artisan__mobile-item & {
        margin: 3rem auto 1rem;
    }
}

.sec-artisan__subs-txt {
    font-size: $fontSizeS;
}

.sec-artisan__desc {
    font-size: $fontSizeM;
    position: relative;
    width: 50%;
    line-height: 1.5;

    @include tabletView() {
        font-size: $fontSizeS;
        width: 100%;
        left: 0;
    }
}

.sec-artisan__info {
    position:relative;
    font-size: $fontSizeSS;
}

.sec-artisan__info-item {
    padding:.5rem 0;
    border-bottom: 1px solid rgba(#FFF, .2);
    strong{
        font-size: $fontSizeS;
        font-weight: normal;
    }
    &:last-child {
        border-bottom: 0;
    }
}

.sec-artisan__parallax{
    &>div{
        &>div{
            position: relative;
            &:last-child{
                position: absolute;
                width:100%;
                height:100%;
            }
        }
    }
}