@import "../../styles/AppDesign.scss";

.cube-number{
    width:100%;
    position:relative;
    &:before{
        content:'';
        display:block;
        padding-bottom:100%;
    }
    // Reference
    // background:url('/img/logo/logo-main-white.svg') no-repeat center center;
    // background-size:contain;
    // &:after{
    //     content:'';
    //     display:block;
    //     padding-bottom:100%;
    // }
}
.cube-number__cubes{
    position:absolute;
    $scale:0.6;
    width:100% * $scale;
    top:100% * (1 - $scale)/2;
    left:100% * (1 - $scale)/2;
    &:before{
        content:'';
        display:block;
        padding-bottom:100%;
    }
}
@mixin moveAlongCube($num) {
    $x: $num*28.5%;
    $y: $num*-17.9%;
    transform: translate($x, $y);
}
.cube-number__cube{
    position:absolute;
    width:100%;
    height:100%;
    left:0;
    top:0;
    &--left{
        @include moveAlongCube(-1);
    }
    &--right{
        @include moveAlongCube(1);
    }
}
.cube-number__cube-graphic{
    position:absolute;
    width:100%;
    height:100%;
    left:0;
    top:0;
    background:url() no-repeat center center;
    background-size:contain;
    
    opacity:1;
    filter: invert(1);

    .splash--step-1 &{
        animation: logo-enlarge-number $ani-time-splash-step1 ease-in;
    }
    @keyframes logo-enlarge-number {
        0%   {
            opacity:0.2;
            filter: invert(0);
        }
        100% {
            opacity:1;
            filter: invert(1);
        }
    }

    // Adjust for different number
    // 0
    .cube-number__cube--0.cube-number__cube--left &{
        transform-origin: center center;
        transform: scale(1) translate(2.4%, -3%);
    }
    .cube-number__cube--0.cube-number__cube--right &{
        transform-origin: center center;
        transform: scale(1) translate(5.2%, -1.5%);
    }

    // 3
    .cube-number__cube--3.cube-number__cube--left &{
        transform-origin: center center;
        transform: scale(1) translate(2.4%, -3%);
    }
    .cube-number__cube--3.cube-number__cube--right &{
        transform-origin: center center;
        transform: scale(1) translate(5.2%, -1.5%);
    }
    // 5
    .cube-number__cube--5.cube-number__cube--left &{
        transform-origin: center center;
        //transform: scale(1.08) translate(2%, 2.2%);
        transform: scale(1.08) translate(0, 0);
    }
    .cube-number__cube--5.cube-number__cube--right &{
        transform-origin: center center;
        //transform: scale(1.08) translate(2%, 2.2%);
        transform: scale(1.08) translate(3.7%, 0.1%);
    }

    // 8
    .cube-number__cube--8.cube-number__cube--left &{
        transform-origin: center center;
        transform: scale(1) translate(2.4%, -3%);
    }
    .cube-number__cube--8.cube-number__cube--right &{
        transform-origin: center center;
        transform: scale(1) translate(5.2%, -1.5%);
    }
}

.cube-number__nwd{
    position: absolute;
    width:46%;
    &:before{
        content:'';
        display:block;
        padding-bottom:100%;
    }
    // @include moveAlongCube(-2.5);
    width: 48.4%;
    transform: translate(-68.8%, 21%);
    left:0;
    bottom:0;
    background:url() no-repeat center center;
    background-size:contain;
}