
@mixin fontFace($family, $src, $style: normal, $weight: normal) {
  @font-face {
    font-family: $family;
    src: url("#{$src}.otf") format("opentype");
    // src: url("#{$src}.eot"); // IE9 compat
    // src: url("#{$src}.eot?#iefix") format("embedded-opentype"),
    //   // IE8 and below
    //     url("#{$src}.woff") format("woff"),
    //   // standards
    //     url("#{$src}.ttf") format("truetype"),
    //   // Safari, Android, iOS
    //     url("#{$src}.svg##{$family}") format("svg"); // legacy iOS
    font-style: $style;
    font-weight: $weight;
  }
}
@mixin a11y-hidden {
  position:absolute;
    left:-10000px;
    top:auto;
    width:1px;
    height:1px;
    overflow:hidden;
}
@mixin global-content-grid(){
  max-width:$gridMedium;
  width:90%;
}

@mixin desktop-content-grid(){
  max-width: 560px;
  width:100%;
}

@mixin sec-grid() {
  width: 50%;
  @include mobileView(){
    width: 80%;
  }
}

@mixin resetBtn() {
  appearance: none;
  border:0;
  box-shadow: none;
  background: transparent;
  padding:0;
  cursor: pointer;
}

@mixin gray-card(){
  width: 100%;
  background-color: $dark;
  padding: 1rem;
  border-radius: 0.7rem;
  max-width: 480px;
}

@mixin centerXY() {
  left:50%;
  top:50%;
  transform: translate(-50%, -50%);
}