@import "../../styles/AppDesign.scss";

.sec-tour{
    position: relative;
    // min-height: 100vh;
    width: 100%;
    z-index: 600;
    top:0;
    left:0;
    padding-top:100px;
    padding-left:30px;
    cursor: url('/img/icon/icon-stay-tune.png'), auto;
    @include tabletView(){
        padding-top:60px;
    }
}
.sec-tour__inner{
    position: relative;
    margin: 0 auto;
    @include sec-grid();
}

.sec-tour__title{
    @include secTitle();
}
.sec-tour__desc{
    @include secDesc();
    position: relative;
    width:50%;
    left:50%;
    @include tabletView(){
        width:100%;
        left:0;
    }
}
