@import "../../styles/AppDesign.scss";

.sec-exhibition{
    position: relative;
    min-height: 100vh;
    width: 100%;
    z-index: 600;
    top:0;
    left:0;
    padding-top:100px;
    padding-left:30px;
}

.sec-exhibition__bg{
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    pointer-events: none;
    z-index: 510;
    overflow: hidden;
    background:#FFF
}
.sec-exhibition__bg-graphic{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 75%;
    background: url('/img/exhibition/bg-exhibtion.svg') no-repeat center center;
    background-size:cover;
}
.sec-exhibition__inner{
    position: relative;
    margin: 0 auto;
    @include sec-grid();
}
.sec-exhibition__title{
    @include secTitle();
}
.sec-exhibition__desc{
    @include secDesc();
    position: relative;
    width:50%;
    left:50%;
    @include tabletView(){
        width:100%;
        left:0;
    }
}
.sec-exhibition__perspective{
    position:relative;
    width:150%;
    margin:2rem 0;
    @include tabletView(){
        width:100%;
    }
    @include mobileView(){
        width:100%;
    }
}
.sec-exhibition__perspective-deco{
    position: absolute;
    left:100%;
    bottom:100%;
    z-index: 50;
    &:before{
        content:'';
        $size:60px;
        $offset: 2rem;
        width: $size;
        height: $size;
        margin-left:-$offset;
        margin-bottom:-$offset;
        display: block;
        background: url('/img/exhibition/icon-quote.svg') no-repeat center center;
        background-size:contain;
        @include mobileView(){
            $size:30px;
            width: $size;
            height: $size;
        }
    }
}
.sec-exhibition__perspective-bg{
    position: absolute;
    width:100%;
    height:100%;
    top:0;
    left:0;
    z-index: 0;
    background: rgba($main, 1);

}
.sec-exhibition__perspective-content{
    position: relative;
    padding:2rem;
    z-index: 30;
    @include mobileView(){
        width:100%;
        padding:1rem 2rem;
    }
}
.sec-exhibition__perspective-text{
    position: relative;
    font-size: $fontSizeTitleS;
    z-index: 50;
    @include mobileView(){
        font-size: $fontSizeL;
    }
}
.sec-exhibition__perspective-tag{
    position: relative;
    font-size: $fontSizeM;
    z-index: 50;
}

.sec-exhibition__gallery{
    position: relative;
    margin-top:10rem;
    padding-top:3rem;
    @include tabletView(){
        margin-top:6rem;
    }
}
.sec-exhibition__gallery-graphic{
    position: absolute;
    pointer-events: none;
    z-index: 0;
    width: 100%;
    height: 100%;
    top:0;
    left:0;
    @include tabletView(){
        width: 90%;
    }
    &:before,
    &:after{
        content:'';
        position: absolute;
        width: 120%;
        //padding-bottom: 62.5%;
        height:calc(100% + 300px);
        top:-8rem;
        left:-10%;
        border:1px solid $main;
        transform: perspective(1000px) rotateY(10deg);
        @include tabletView(){
            width: calc(100% + 60px);
            height:calc(100% + 150px);
            left:-30px;
            top:-5rem;
        }
    }
    &:after{
        margin-left:2rem;
        margin-top:2rem;
        @include tabletView(){
            display:none;
        }
    }
}
$gutter: 30px;
.sec-exhibition__gallery-infos{
    position:relative;
    z-index: 10;
    width:100%;
    display:flex;
    margin-right: -$gutter/2;
    margin-left: -$gutter/2;
    padding-bottom:3rem;
    @include tabletView(){
        display:block;
        margin-right: 0;
        margin-left: 0;
        padding: 0;
    }
}
.sec-exhibition__gallery-info{
    width:50%;
    padding-right: $gutter/2;
    padding-left: $gutter/2;
    display:flex;
    @include tabletView(){
        width:100%;
        padding-right: 0;
        padding-left: 0;
        margin-bottom:3rem;
    }
//     justify-content: flex-end;
//     &:nth-child(2){
//         justify-content: flex-start;
//     }
}
.sec-exhibition__gallery-info-content{
    position: relative;
    width:100%;
}
.sec-exhibition__slogans{
    width:75%;
    max-width: 360px;
}
.sec-exhibition__info-tag{
    font-size: $fontSizeS;
    display:block;
    margin-bottom:.5rem;
}
.sec-exhibition__info-title{
    font-size: $fontSizeTitleM;
    display:block;
}
.sec-exhibition___slider{
    position: relative;
    z-index: 30;
}