@import "./styles/normalize.scss";
@import "./styles/AppDesign.scss";

html,
body {
  height: 100%;
}

html,
body,
#root {
  width: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-touch-callout: none;
}

#root {
  // overflow-x: hidden;
}
main {
  width: 100%;
  overflow-x: hidden;
}

input,
button {
  outline: none;
  -webkit-appearance: none;
}

html {
  font-family: $fontSecondary;
  @include largeScreen() {
    font-size:0.8em;
  }
  @include tabletView() {
    font-size:0.8em;
  }
}

body {
  // overflow: hidden;
  // position: fixed;
  background: $lightGray;
  margin: 0;
  font-family: $fontSecondary;
  -moz-osx-font-smoothing: grayscale;
  color:$colorTxt;
}

* {
  box-sizing: border-box;
}


.align-center{
  display: flex;
  justify-content: center;
  align-items: center;
}
// Global Helpers
.a11y-hidden {
  @include a11y-hidden();
}
.hidden-desktop {
  @include desktopView() {
    display: none !important;
  }
}
.hidden-mobile {
  @include mobileView() {
    display: none !important;
  }
}
.visible-desktop {
  @include mobileView() {
    display: none !important;
  }
}
.visible-mobile {
  @include desktopView() {
    display: none !important;
  }
}

.color-main{
  color: $main;
}