@import "../../styles/AppDesign.scss";
$transTime: .6s;
.deck-slider {
    position: relative;
    width: 75%;
    margin:0 auto;
    // &:before{
    //     content: '';
    //     display:block;
    //     padding-bottom:100%;
    //     background: rgba(#000, 0.5);
    // }
    @include mobileView(){
        width: 80%;
        margin:0;
    }
}

.deck-slider__slider{
    // width: 200px;
    // display: flex;
    // align-items: center;
    // margin:0 auto;
    position: relative;
    left:-15%;
    width: 100%;
    margin:0 auto;
    font-size: 0;
    &:before{
        content: '';
        display:block;
        padding-bottom:62.5%;
    }
    @include mobileView(){
        left:0%;
    }
}
.deck-slider__slide{
    position: absolute;
    width: 100%;
    height:100%;
    right:0;
    top:0;
    // position: relative;
    // width: 10vw;
    // display: flex;
    // justify-content: center;
    // margin:0 auto;
    z-index: 10;
    order:1;
    transition: $transTime;
    &.is-active{
        z-index: 30;
        pointer-events: none;
        order: 0 !important;
    }
}
.deck-slider__slide-body{
    position: absolute;
    width: 100%;
    height:100%;
    left:0;
    top:0;
    transition: $transTime;
    transform-origin: right center;
}
.deck-slider__click-area{
    cursor: pointer;
    border:0;
    padding:0;
    background: transparent;
    font-size: 0;
    opacity: 0;
    position: absolute;
    z-index: 30;
    width: 100%;
    height: 100%;
    top:0;
    left:0;
}
.deck-slider__img{
    // position: relative;
    // width: 33.3333vw;
    // flex: 0 0 auto;
    position: absolute;
    width: 100%;
    height:100%;
    left:0;
    top:0;
    background: no-repeat center center;
    background-size: cover;
    transition: $transTime;
    // &:before{
    //     content: '';
    //     display:block;
    //     padding-bottom:75%;
    // }
    .deck-slider__slide.is-active &{
        transform: perspective(1000px) rotateY(-10deg);
        left:-3%;
    }
}
.deck-slider__captions{
    margin-top:2rem;
    display:flex;
    justify-content: flex-end;
    font-size: $fontSizeM;
    line-height: 1.5;
    @include mobileView(){
        justify-content: flex-start;
    }
}
.deck-slider__date{
    color:$main;
}
.deck-slider__location{
    position: relative;
    $iconSize:24px;
    padding-left: $iconSize + 5px;
    &:before{
        content: '';
        display:block;
        width: 20px;
        height: 20px;
        position: absolute;
        left: 0;
        top: .15em;
        background: url('/img/exhibition/icon-location.svg') no-repeat center center;
        background-size: contain;
    }
}
.deck-slider__remark{
    opacity: 0.5;
}