@import "../../styles/AppDesign.scss";

.accordion {
  width: 100%;
  max-width: 1080px;
  margin: 0 auto;
  border-top: 1px solid rgba(#FFF, .2);
  border-bottom: 1px solid rgba(#FFF, .2);

  // &:last-child{
  //   border-bottom:1px solid #000;
  // }
  @include smallScreen() {
    border: 0;
  }
}

.accordion-toggle {
  position: relative;
  width: 100%;
  appearance: none;
  background: transparent;
  border: 0;
  padding: 1.5rem 0;
  padding-right: 2rem;
  border-top: 1px solid 1px solid rgba(#FFF, .2);
  text-align: left;
  font-family: $fontPrimary;
  cursor: pointer;

  .accordion:first-child & {
    border-top: 0;

    .accordion-parent & {
      border-top: 1px solid #FFF;
    }

    .accordion-parent:first-child & {
      border-top: 0;
    }
  }


  @include smallScreen() {
    background: transparent;
    border: 0;
  }
}

.accordion-toggle-graphic{
  position:relative;
  display: inline-block;
  vertical-align: middle;
  $size:.8em;
  width:$size;
  height:$size;
  margin-left:0.25rem;
  margin-top:-0.5rem;
  transition: .4s;
  &:before ,
    &:after {
      content: '';
      position: absolute;
      height:1px;
      width:100%;
      left:0;
      top:50%;
      background:#FFF;
      .accordion.mod-open & {
        background:red;
      }
    }
  &:after {
    transform: rotate(-90deg);
  }
  .accordion.mod-open & {
    transform: rotate(45deg);
  }
}

.accordion-toggle-title {
  display: inline-block;
  vertical-align: middle;
  margin-right: .5rem;
  font-size: $fontSizeTitleSS;
  font-weight: 700;
  color: #FFF;

  @include smallScreen() {
    text-align: center;
  }
}

.accordion-content {
  width: 100%;
  display: none;

  .accordion.mod-open & {
    display: block;
  }

  padding:0 0 .5rem;

  @include smallScreen() {
    .accordion.mod-open & {
      border-top: 1px solid transparent;
    }
  }
}

.accordion-bottom-control {
  margin-top: 5rem;
  text-align: right;

  @include smallScreen() {
    margin: 0;
    padding: 2rem 3%;
  }
}

.accordion-bottom-close {
  position: relative;
  appearance: none;
  padding: .5rem 1.5rem;
  background: $main;
  border: 0;
  box-shadow: none;
  color: #FFF;
  font-size: $fontSizeM;
  font-weight: bold;
  font-family: $fontPrimary;
  border-radius: 3rem;
  cursor: pointer;

  &:hover {
    background: rgba($main, 0.8);
  }

  &:after {
    content: '';
    display: inline-block;
    vertical-align: middle;
    $size: .4rem;
    width: $size;
    height: $size;
    border-top: $size/3 solid #FFF;
    border-left: $size/3 solid #FFF;
    transform: rotate(45deg);
    transition: .4s;
    margin-left: .5rem;
  }
}