@import "../../styles/AppDesign.scss";

footer{
    position:relative;
    background:#1E1E1E;
    z-index: 3000;
    color:#FFF;
}

.footer__inner{
    position: relative;
    width:94%;
    margin: 0 auto;
    @include tabletView(){
        width:90%;
        max-width:calc(100% - 60px);
    }
}
.footer__title{
    font-size: $fontSizeTitleS;
    margin:0 0 3rem;
}
.footer__top{
    padding:3rem 0;
    border-bottom:1px solid rgba(#FFF, .2);
}
.footer__collabs{
    display:flex;
}
.footer__collab-grp{
    width:100%;
}
.footer__collab-title{
    font-size: $fontSizeS;
    margin:0 0 3rem;
    text-transform: uppercase;
}
.footer__logos{
    display:flex;
    .footer__nwd50 &{
        display:flex;
    }
    @media (max-width: 480px) {
        display: block;
    }
}
.footer__logo{
    margin-right:2rem;
    &:last-child{
        margin-right:0;
    }
    &--k11{
        max-width: 180px;
        @include mobileView{
            width:80%;
            max-width: 140px;
        }
    }
    &--ddb{
        max-width: 120px;
        @include mobileView{
            width:70%;
            max-width: 80px;
        }
    }
    &--msl{
        max-width: 120px;
        margin-left:5rem;
        align-self: flex-end;
        @include mobileView{
            margin-left:0;
            margin-top:2rem;
            display: block;
            width:70%;
            max-width: 80px;
        }
    }
    &--nwd50{
        max-width: 120px;
        @include mobileView{
            width:70%;
            max-width: 80px;
        }
    }
    &--slogans{
        max-width: 180px;
        @include mobileView{
            width:70%;
            max-width: 120px;
        }
    }
    &--tam{
        width:70%;
        max-width: 520px;
        @include mobileView{
            width: 70%;
        }
    }
}
.footer__logo-br{
    height:90px;
    width:1px;
    background:#FFF;
    margin-right:2rem;
    @include mobileView{
        height:70px;
    }
}
.footer__nwd50{
    margin-bottom:2rem;
}
.footer__bottom{
    padding:2rem 0 1rem;
}
.footer__info{
    width:100%;
    display:flex;
    justify-content: space-between;
    align-items: center;
    @include mobileView{
        display:flex;
        flex-flow: column-reverse;
        align-items: flex-start;
    }
    .footer__logos{
        display:flex;
        justify-content: flex-end;
        @include mobileView{
            justify-content: flex-start;
        }
    }
}
.footer__copyright{
    font-size: $fontSizeSS;
    color:rgba(#FFF, .5);
    @include mobileView{
        margin-top:1rem;
    }
}