@import "../../styles/AppDesign.scss";

.sec-video{
    position: relative;
    height: 100vh;
    width: 100%;
    z-index: 600;
    top:0;
    left:0;
}

.sec-video__inner{
    position: relative;
    top:0;
    height: 100%;
    width: 100%;
    display:flex;
    align-items: center;
    justify-content: center;
}
.sec-video__overlay{
    position: fixed;
    background:#000;
    top: 0;
    left:0;
    height: 100%;
    width: 100%;
    pointer-events: none;
    z-index: 550;
}

.sec-video__video{
    position:relative;
    width: 100%;
    height:auto;
    // max-width:960px;
    transition: .1s;
    &:before{
        position:relative;
        content:'';
        display:block;
        padding-bottom:75%;
    }
    video{
        position: absolute;
        left:0;
        top:0;
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
}

.sec-video__process-info{
    position:fixed;
    width:200px;
    height:200px;
    background:rgba(#fff, 0.5);
    color:#000;
    padding:15px;
    z-index: 1000;
    bottom:0;
    right:0;
    word-break: break-all;
}